import cTitle from 'components/title';
import { MessageBox } from 'mint-ui';
import { Indicator, Toast } from 'mint-ui';
export default {
  data() {
    return {
      checkList: [],
      datas: [],
      checkAll: false
    }
  },

  activated() {

    this.getData();
  },
  methods:
  {

    // handleCheckAllChange(event) 
    // {
    //   this.checkList = event.target.checked ? this.datasoruce : [];

    // },
    // handleCheckedChange(event) 
    // {
    //   console.log(event);

    //   // let checkedCount = value.length;
    //   // this.checkAll = checkedCount === this.datasoruce.length;

    // },


    getData() {
      let that = this;
      let json = {};

      $http.get('member.member-history.index', json).then(function (response) {
        console.log(response)
        if (response.result == 1) {
          that.datas = response.data;
        } else {

        }

      }, function (response) {
        console.log(response)
      });
    },

    delteteItem(item, i) {
      console.log(i);
      let that = this;
      $http.get('member.member-history.destroy', { id: item.id }, "处理中...").then(function (response) {
        console.log(response)
        if (response.result == 1) {
          that.datas.splice(i, 1);
          Toast("操作成功");
        } else {
          Toast(response.msg);
        }

      }, function (response) {
        console.log(response);
      });

    },

    toItem(goods)
    {
      //console.log(goods);
      this.$router.push({ name: 'goods', params: { id: goods.id } });
    }


  },

  


  components: { cTitle }
}


